import React, { Component } from 'react'
import { connect } from 'dva'
import { Button, Select } from 'antd'
import BaseForm from '@/components/BaseForm'
import BaseTable from '@/components/BaseTable'
import BaseContent from '@/components/BaseContent'
import styles from './style.module.less'
import { getQusetions } from '@/service/questionsManger'
import UtilGlobal from '@/utils/global'

const { basePath } = UtilGlobal;
const mapStateToProps = store => {
    return { ...store.examValueate, ...store.main }
}
const mapDispatchToProps = dispatch => {
    return {
        initData(payload) {
            dispatch({ type: 'examValueate/requestData', ...payload });
        },
        getVlueateList(payload) {
            dispatch({ type: 'examValueate/evalueate', ...payload });
        },
        changePage(page, pageSize) {
            let { pagination } = this.props;
            dispatch({
                type: 'examValueate/change',
                payload: {
                    ...pagination,
                    page,
                    pageSize
                }
            })
        },
        changeSize(page, pageSize) {
            let { pagination } = this.props;
            console.log(pagination)
            dispatch({
                type: 'examValueate/change',
                payload: {
                    ...pagination,
                    page,
                    pageSize
                }
            })
        }

    }
}

const TABLE_KEY = 'evalueat_classlists';
const Option = Select.Option;

@connect(mapStateToProps, mapDispatchToProps)
class ClassMate extends Component {
    componentDidMount() {
        const { gradeId } = this.props.history.location.state;
        this.props.initData()
        this.props.getVlueateList({gradeId})
    }

    get renderForm() {
        const { gradeList } = this.props;
        return (
            <BaseForm
                items={[
                    {
                        label: '状态',
                        field: 'status',
                        content: (
                            <Select>
                                {/* {examType.map((item, index) => <Option value={item.exam_id} key={index}>{item.exam_name}</Option>)} */}
                            </Select>
                        ),
                    },
                    {
                        label: '班级',
                        field: 'classes',
                        content: (
                            <Select>
                                {gradeList.map((item, index) => <Option value={item.grade_id} key={index}>{item.grade_name}</Option>)}
                            </Select>
                        ),
                    },
                    {
                        isFormItem: false,
                        content: (
                            <Button
                                type="primary"
                                icon="search"
                                htmlType="submit"
                                style={{
                                    margin: '3px 0 0 50px',
                                }}
                            >查询
                    </Button>
                        ),
                    }
                ]}
                onSubmit={this.handleFormSubmit}
            />
        )
    }
    classInfo(gradeID) {
        const { gradeList } = this.props;
        return gradeList.length && gradeList.filter(classes => {
            return classes.grade_id === gradeID;
        })
    }
    handlerClick(exam_student_id) {
        const { history } = this.props;
        history.push({
            pathname: basePath + `/main/paper/detail/`,
            search: `?paper_id=${exam_student_id}`,
            state: {
                parent: '阅卷管理',
                child: '阅卷',
                id: 'main-examinationPapers'
            }
        });
    }

    async getQusetionsById(id) {
        return await getQusetions({ questions_id: id });
    }
    get columns() {
        const columns = [{
            title: '班级',
            key: 'exam_class',
            dataIndex: 'grade_id',
            render: (gradeID) => {
                let classInfo = this.classInfo(gradeID);
                return (
                    <React.Fragment>
                        {classInfo.length && classInfo[0].grade_name}
                    </React.Fragment>
                );
            }
        }, {
            title: '姓名',
            dataIndex: 'student_name',
            key: 'student_name'
        }, {
            title: '阅卷状态',
            dataIndex: 'status',
            key: 'evalueate_statu',
            render: (status) => {
                return (
                    <React.Fragment>
                        {status === 0 ? '未阅' : '已阅'}
                    </React.Fragment>
                );
            }
        }, {
            title: '开始时间',
            dataIndex: 'start_time',
            key: 'start_time',
        }, {
            title: '结束时间',
            dataIndex: 'end_time',
            key: 'end_time',
        }, {
            title: '成材率',
            dataIndex: 'score',
            key: 'score',
            render: (score) => {
                return (
                    <React.Fragment>
                        {score === 0 ? '-' : score}
                    </React.Fragment>
                );
            }
        }, {
            title: '操作',
            key: 'action',
            render: (item) => {
                return (
                    <React.Fragment>
                        <div>
                            <a href="javascript:;" onClick={() => this.handlerClick(item.exam_student_id)}>批卷</a>
                        </div>
                    </React.Fragment>
                );
            }
        }];
        return columns
    }
    get renderTable() {
        const { valueateList, pagination, changePage, changeSize } = this.props
        return (
            <BaseTable
                rowKey={TABLE_KEY}
                columns={this.columns}
                dataSource={valueateList}
                wrapStyle={{
                    padding: 0,
                }}
                buttons={
                    [
                        <div className={styles.tool}>
                            <h4 style={{ margin: 0 }}>试卷列表</h4>
                        </div>
                    ]
                }
                pagination={{
                    ...pagination,
                    onChange: () => changePage,
                    onShowSizeChange: () => changeSize
                }}
            />
        )
    }
    render() {

        return (
            <React.Fragment>
                <BaseContent>
                    {this.renderForm}
                </BaseContent>
                <BaseContent>
                    {this.renderTable}
                </BaseContent>
            </React.Fragment>
        )
    }
}
export default ClassMate;
